    <div class="plug">
      <div class="steps-caption" @click="stepsOpen = !stepsOpen">
        <button class="steps-caption-btn" :class="{ 'steps-caption-btn__open': stepsOpen }"></button>
      </div>
    </div>
    <div class="steps-block" :class="{ 'steps-block__open': stepsOpen }">
      <div class="steps-caption">

      </div>
      <a-timeline>
        <div class="time-line-element" v-for="  item   in   currentEvents.stages.sort((a, b) => a.order - b.order)  "
          :key="item.id" :class="{
            'event-disabled': new Date(item.startDate) > new Date(),
            'event-process':
              new Date(item.startDate) < new Date() &&
              new Date() < new Date(item.endDate) &&
              getStagePrecent(item) !== 100,
            'event-sucses':
              new Date() > new Date(item.endDate) ||
              getStagePrecent(item) === 100,
          }
            ">
          <a-timeline-item style="margin-left: auto">
            <div class="time-line-filling">
              <p class="date">
                {{
                  new Date(item.startDate).toLocaleDateString(
                    "ru-RU",
                    dateOptions
                  ) +
                  " - " +
                  new Date(item.endDate).toLocaleDateString(
                    "ru-RU",
                    dateOptions
                  )
                }}
              </p>
              <div class="timeline-title">
                <fund-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'test'" />
                <video-camera-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'interview'" />
                <schedule-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'form'" />
                <profile-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'exercise'" />
                <customer-service-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'stream'" />
                <a-typography-title :level="5">
                  {{
                    item.type === "test"
                    ? (item?.testing?.title ? `${item?.testing?.title} ` : 'Тестирование')
                    : item.type === "form"
                      ? (item?.testing?.title ? `${item?.testing?.title} ` : 'Анкета')
                      : item.type === "interview"
                        ? "Видео-интервью"
                        : item.type === "exercise"
                          ? "Практическое задание"
                          : item.type === "stream"
                            ? "Трансляция"
                            : null
                  }}
                </a-typography-title>

              </div>
              <a-typography-text v-if="item?.type === 'form' && getShortDescription(item)" type="secondary"
                class="text text-second">
                {{
                  textSymbolLimit(getShortDescription(item), viewMoreDescription === item.id ? getShortDescription()?.length
                    : descriptionSymbolLimit)
                }}
                <button style="font-weight: 500" v-if="getShortDescription(item)?.length > descriptionSymbolLimit"
                  @click="viewMoreDescription = (viewMoreDescription === item.id ? null : item.id)">
                  {{ viewMoreDescription === item.id ? 'Скрыть' : 'Показать больше' }}
                </button>
              </a-typography-text>

              <a-typography-text v-if="(item.type === 'exercise' || item.type === 'test') &&
                checkScore(item) === 'score'
                " type="secondary" class="text">
                Правильность прохождения:
                {{
                  Math.trunc(
                    (eventPassing.testingPassing[
                      eventPassing.testingPassing
                        .map((pass) => pass.testingId)
                        .indexOf(item.testId)
                    ].finalScore /
                      item.maxScore) *
                    100
                  )
                }}%
              </a-typography-text>
              <br />

              <a-button :class="{
                    blocked: [
                      'Не началось',
                      'Пройдено',
                      'Ожидает проверки',
                      'Завершено',
                      'Не пройдено',
                      'Заполненo',
                    ].includes(getButtonText(item)),
                  }
                  " @click="goToStage(item)">
                {{ getButtonText(item) }}
              </a-button>
            </div>
            <template #dot>
              <a-progress :stroke-width="4" :format="() => item.order" :stroke-color="'#0fcbcc'" type="circle"
                status="active" :percent="getStagePrecent(item)" :width="32" />
            </template>
          </a-timeline-item>
        </div>

        <div class="time-line-element" :class="{
          'event-unapproved': eventPassing?.passingStatus !== 'passed',
          'event-sucses': eventPassing?.passingStatus === 'passed',
        }
          ">
          <a-timeline-item style="margin-left: auto">
            <template #dot>
              <a-progress :stroke-width="4" :stroke-color="eventPassing?.passingStatus === 'passed'
                ? '#0fcbcc'
                : '#808080'
                " type="circle" :percent="100" :width="32" />
            </template>
          </a-timeline-item>
        </div>
      </a-timeline>
