<script>
import { mapGetters } from "vuex";
import {
  // FieldTimeOutlined,
  // CheckCircleOutlined,
  // SyncOutlined,
  // MinusCircleOutlined,
  CheckOutlined,
  FundOutlined,
  VideoCameraOutlined,
  ScheduleOutlined,
  ProfileOutlined,
  CustomerServiceOutlined,
  CalendarOutlined,
  TeamOutlined,
  EnvironmentOutlined
} from "@ant-design/icons-vue";
import { errorHandler } from "@/lib/errorHandler";
import { textSymbolLimit } from "@/lib/textSymbolLimit";
import SeparateUser from '@/components/stream/SeparateUser.vue'
import {convertFileSize} from "../../lib/convertTime";
// import StatisticWindow from "@/components/events/StatisticWindow";

export default {
  name: "SeparateEventsPage",
  props: ["eventId"],
  components: {
    // StatisticWindow,
    // CheckCircleOutlined,
    // SyncOutlined,
    // MinusCircleOutlined,
    // FieldTimeOutlined,
    CheckOutlined,
    FundOutlined,
    VideoCameraOutlined,
    ScheduleOutlined,
    ProfileOutlined,
    CustomerServiceOutlined,
    SeparateUser,
    TeamOutlined,
    CalendarOutlined,
    EnvironmentOutlined
  },
  computed: {
    ...mapGetters(["currentEvents", "eventPassing", "role", "stream"]),
    additionalData() {
      if (this.currentEvents?.additionalData) {
        return JSON.parse(this.currentEvents?.additionalData);
      }
      return null;
    }
  },
  watch: {
    viewStream: function (val) {
      if (!val) {
        this.$store.commit("setStreamData", null);
      }
    },
    // openUsersList(val) {
    //   if (val) {
    //     const stageId = this.currentEvents?.stages[0]?.id;
    //
    //     if (!stageId) return;
    //
    //     this.$store.dispatch('fetchUsersByStageId', {
    //       stageId
    //     }).then(data => {
    //       this.usersList = data;
    //     });
    //   } else {
    //     this.disabeldViewUserListButton = true;
    //     setTimeout(() => {
    //       this.disabeldViewUserListButton = false;
    //     }, 2000)
    //   }
    // }
  },
  beforeMount() {
    scroll(0, 0);
    this.$store
      .dispatch("fetchCurrentEvents", this.eventId)
      .then(async (data) => {
        if (this.role !== 'admin') {
          if (!data || new Date() > new Date(data.endDate)) {
            this.$router.push({ name: "allEvents" });
            return;
          }
        }
        document.dispatchEvent(
          new CustomEvent("setTitle", { detail: data.title })
        );
        await this.$store.dispatch(
          "fetchEventPassing",
          data.eventPassings.length ? data.eventPassings[0].id : null
        );
        this.eventProgram = await this.$store.dispatch('fetchEventProgram', this.eventId);
        this.eventFiles = await this.$store.dispatch('fetchEventFiles', this.eventId);
        this.eventFiles.map(item=>{
          item.sideData = JSON.parse(item.sideData);
          return item
        })

        const stage = this.currentEvents?.stages[0];
        if (!stage) return;


        if (stage.type === 'interview') {
          this.$store.dispatch('fetchInterviewById', stage?.interviewId).then(() => {
            this.$store.getters.interview?.interviewStageSlots?.map(item => {
              for (let i = 0; i < item?.usersInSlots.length; i++) {
                if (this.usersList.map(item => item.id).includes(item?.usersInSlots[i]?.client?.id)) {
                  continue;
                }
                this.usersList.push(item?.usersInSlots[i]?.client);
              }
            })
          });
        } else {
          this.$store.dispatch('fetchUsersByStageId', {
            stageId: stage?.id
          }).then(data => {
            if (data?.[0]?.testing?.testingPassing) {
              for (let i = 0; i < data?.[0]?.testing?.testingPassing?.length; i++) {
                if (this.usersList.map(item => item.id).includes(data?.[0]?.testing?.testingPassing[i].eventPassing?.client?.id)) {
                  continue;
                }
                this.usersList.push(data?.[0]?.testing?.testingPassing[i].eventPassing?.client);
              }
            }
          });
        }


      });
  },
  data() {
    return {
      CheckOutlined,
      dateOptions: {
        month: "long",
        day: "numeric",
      },
      viewStatistic: false,
      openUsersList: false,
      stepsOpen: false,
      viewStream: false,
      windowWidth: window?.innerWidth,
      descriptionSymbolLimit: 120,
      viewMoreDescription: null,

      eventFiles: [],
      eventProgram: null,

      usersList: [],
      disabeldViewUserListButton: false
    };
  },
  methods: {
    convertFileSize,
    textSymbolLimit,
    pickStream(stage) {
      this.$store.dispatch("fetchStreamById", stage.translationId).then(() => {
        this.viewStream = true;
      });
    },
    getShortDescription(item) {
      try {
        const sideData = JSON.parse(item?.testing?.sideData);
        return sideData?.shortDescription;
      } catch (e) {
        return null;
      }
    },
    checkScore(item) {
      if (this.eventPassing) {
        if (item.type === "interview") {
          // let index = this.eventPassing.event.stages
          //     .map((stage) => stage.id)
          //     .indexOf(item.id);
          // if (index !== -1) {
          //   if (
          //       this.eventPassing.event.stages[index].interviewBookedTime &&
          //       this.eventPassing.event.stages[index].interviewBookedTime
          //           .length &&
          //       this.eventPassing.event.stages[index].interviewBookedTime[0]
          //           .description === "" && (this.eventPassing.event.stages[index].interviewBookedTime[0].whenDate &&
          //           new Date(this.eventPassing.event.stages[index].interviewBookedTime[0].whenDate) < new Date())
          //   ) {
          //     return "pass";
          //   } else {
          //     return "score";
          //   }
          // } else {
          //   return null;
          // }
          return "score";
        } else {
          let index = this.eventPassing.testingPassing
            .map((pass) => pass.testingId)
            .indexOf(item.testId);
          if (index !== -1) {
            if (this.eventPassing.testingPassing[index].finalScore != null) {
              return "score";
            } else {
              return "pass";
            }
          }
        }
      } else {
        return null;
      }
    },
    getButtonText(item) {
      if (new Date(item.startDate) > new Date()) {
        return "Не началось";
      }
      if (item.type === "form") {
        const index =
          this.eventPassing && this.eventPassing.testingPassing
            ? this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId)
            : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null)
            return "Заполненo";
          return "Заполнено";
        }
        return "Заполнить";
      }

      if (item.type === "test" || item.type === "exercise") {
        const index =
          this.eventPassing && this.eventPassing.testingPassing
            ? this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId)
            : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null)
            return "Пройдено";
          return "Ожидает проверки";
        }
        return "Пройти";
      }

      if (item.type === "interview") {
        if (new Date() > new Date(item.endDate)) {
          return "Завершено";
        }

        return "Календарь";
      }

      if (item.type === "stream") {
        if (new Date() > new Date(item.endDate)) {
          return "Завершено";
        }
        return "Смотреть";
      }

      if (new Date() > new Date(item.endDate)) {
        return "Не пройдено";
      }
      return "";
    },
    goToStage(stage) {
      let passingId = this.currentEvents.eventPassings.length
        ? this.currentEvents.eventPassings[0].id
        : "null";

      if (new Date() > new Date(stage.endDate)) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап закончился",
          },
        });
        return;
      }
      if (new Date(stage.startDate) > new Date()) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап не начался",
          },
        });
        return;
      }

      if (stage.type === "stream") {
        this.$router.push({ name: 'streamPage', params: { streamId: stage?.translationId, eventId: this.eventId } })
        return;
      }

      // if (this.eventPassing?.currentStageNumber && stage.order - this.eventPassing?.currentStageNumber !== 0) {
      //   errorHandler({
      //     name: "appointment",
      //     status: 300,
      //     meta: {
      //       body: "Вы не прошли предыдущий этап",
      //     },
      //   });
      //   return;
      // } else if (!this.eventPassing && stage.order > 1) {
      //   errorHandler({
      //     name: "appointment",
      //     status: 300,
      //     meta: {
      //       body: "Вы не прошли предыдущий этап",
      //     },
      //   });
      //   return;
      // }

      if (
        (stage.type !== "interview" &&
          this.eventPassing &&
          this.eventPassing.testingPassing &&
          this.eventPassing.testingPassing
            .map((pass) => pass.testingId)
            .indexOf(stage.testId) !== -1 &&
          stage.type !== "form") ||
        (stage.type === "form" &&
          this.eventPassing &&
          this.eventPassing.testingPassing &&
          this.eventPassing.testingPassing
            .map((pass) => pass.testingId)
            .indexOf(stage.testId) !== -1 &&
          this.eventPassing.testingPassing[
            this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(stage.testId)
          ].finalScore != null)
      ) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Вы уже прошли этот этап",
          },
        });
        return;
      }
      switch (stage.type) {
        case "interview":
          this.$router.push({
            name: "interview",
            params: {
              interviewId: stage.interviewId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "test":
          this.$router.push({
            name: "testing",
            params: {
              testingId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
              score: "null",
            },
          });
          break;
        case "form":
          this.$router.push({
            name: "form",
            params: {
              formId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "exercise":
          this.$router.push({
            name: "exercise",
            params: {
              exerciseId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
      }
    },
    getStagePrecent(item) {
      return item.type !== "interview" && this.checkScore(item) === "score"
        ? item.type === "form"
          ? this.checkScore(item) === "score"
            ? 100
            : 0
          : (this.eventPassing.testingPassing[
            this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId)
          ].finalScore /
            item.maxScore) *
          100
        : 0;
    },
  },
  mounted() {
    // window.onresize = (event) => {
    //   this.windowWidth = event.target?.innerWidth;
    // };

    if (document.getElementById("separateEventCSS")) return;
    const element = document.createElement("link");
    element.rel = "stylesheet";
    element.href = "/css/separateEvent.css";
    element.id = "separateEventCSS";
    document.body.append(element);
  },
  beforeUnmount() {
    document.getElementById("separateEventCSS")?.remove();
